nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2px 30px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

#navbar {
  display: flex;
  align-items: center;
  justify-content: center;
}

#navbar li{
  list-style: none;
  padding: 0 30px;
  position: relative;
}
a {
  color: rgb(0, 0, 0);
  font-weight: 800;
  font-size: 2.1rem;
  font-family:monospace;

}
#navbar li a{
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 700;
  color: rgb(7, 7, 7);
  transition: 0.3s ease-in-out;
  font-family:monospace;

}

#navbar li a:hover{
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 800;
  color:rgb(255, 255, 255);
  font-family:monospace;
  
}

#navbar li a:hover::after{
  content: "";
  width: 30%;
  height: 2px;
  background:rgb(248, 248, 248);  
  position:absolute;
  bottom: -4px;
  left: 20px;
  font-family:monospace;

}